<template>
  <el-card class="wrap" :class="{ active: isF }">
    <div class="id-box">
      id: {{ id }}
      <el-button size="small" @click="fFn">
        {{ isF ? '' : '' }}
      </el-button>
      <span v-if="isR" class="red">...</span>
    </div>
    <el-row :gutter="20">
      <el-col class="item" :span="19">
        <p class="item-show" v-html="showText"></p>
      </el-col>
      <el-col class="item" :span="5">
        <el-button size="small" @click="setAudioTime(false, null)">
          
        </el-button>
        <el-button size="small" @click="recorderFn"></el-button>
      </el-col>

      <el-col class="item" :span="19">
        <el-input
          size="small"
          @keyup.enter="checkText"
          v-model="inputText"
        ></el-input>
      </el-col>
      <el-col class="item" :span="5">
        <el-button size="small" @click="clearText"></el-button>
        <el-button size="small" @click="playRecorder"></el-button>
      </el-col>
    </el-row>
  </el-card>
</template>

<script>
import { onMounted, ref, watch } from 'vue'
import { diffWordsWithSpace } from 'diff'

import Recorder from 'js-audio-recorder'
const recorder = new Recorder()

export default {
  props: {
    id: Number,
    answer: String,
    time: Array,
    courseList: Array
  },
  emits: ['setAudioTime'],
  setup(props, { emit }) {
    const inputText = ref('')
    const showText = ref('')
    const isR = ref(false)
    const isF = ref(false)

    const setAudioTime = (isStop, fn) => {
      emit('setAudioTime', ...props.time, isStop, fn)
    }

    const fFn = () => {
      emit('setF', { isF: !isF.value, cid: props.id })
    }

    const checkText = () => {
      if (inputText.value) {
        const diff = diffWordsWithSpace(inputText.value, props.answer)
        let str = ''
        diff.forEach((obj) => {
          if (obj.removed) {
            str += `<span class="red">${obj.value}</span>`
          } else if (obj.added) {
            str += `<span class="green">${obj.value}</span>`
          } else {
            str += `<span>${obj.value}</span>`
          }
        })
        showText.value = str
      } else {
        showText.value = props.answer
      }
    }

    const clearText = () => {
      showText.value = ''
      inputText.value = ''
    }

    // 
    const recorderFn = () => {
      isR.value = !isR.value
      if (recorder.isrecording) {
        recorder.stop()
        return
      }
      recorder.start()
    }
    // 
    const playRecorder = () => {
      recorder.stopPlay()

      setAudioTime(false, () => {
        recorder.play()
      })
    }

    const checkIsF = () => {
      isF.value = props.courseList.findIndex((item) => item === props.id) !== -1
    }

    watch(props.courseList, () => {
      checkIsF()
    })
    onMounted(() => {
      checkIsF()
    })

    return {
      setAudioTime,
      checkText,
      clearText,
      inputText,
      showText,

      isR,
      isF,
      recorderFn,
      playRecorder,
      fFn
    }
  }
}
</script>

