<template>
  <div class="home-wrap">
    <Title v-if="myList.length" :mp3Url="myList[currentId].mp3Url" />
    <el-row class="sel-wrap">
      <el-col :span="20">
        <el-select
          style="margin-right: 20px;"
          v-model="currentId"
          placeholder=""
        >
          <el-option
            v-for="item in myList"
            :key="item.id"
            :label="item.showTit"
            :value="item.id"
          ></el-option>
        </el-select>
        <el-button @click="setCurrent">
          
        </el-button>
        <span class="c-position">
          {{ pMap[cPid] }} - {{ cId + 1 }}
        </span>
      </el-col>
      <el-col :span="4">
        <el-button class="refrash-page" @click="refreshPage"></el-button>
        <el-button class="refrash-page2" @click="refreshPage2"></el-button>
        <el-button class="refrash-page3" @click="toWords"></el-button>
      </el-col>
    </el-row>
    <template v-if="isShow">
      <Item
        v-for="item in myList[currentId].list"
        :key="item.id"
        :id="item.id"
        :answer="item.answer"
        :time="item.time"
        :courseList="courseList"
        @setAudioTime="setAudioTime"
        @setF="setF"
      />
    </template>
    {{ currentId + 1 }}:
    <a
      :href="
        `https://meiyucongtouxue-audio.vercel.app/my-book-00/Lesson${currentId +
          1}.pdf`
      "
      @click="setCurrent"
      target="_blank"
    >
      
    </a>
  </div>

  <div class="footer">
    
  </div>
</template>

<script>
import {
  defineComponent,
  getCurrentInstance,
  nextTick,
  onMounted,
  ref,
  watch
} from 'vue'
import { ElLoading } from 'element-plus'
import Title from '../components/Title.vue'
import Item from '../components/Item.vue'
import my00 from '../datas/my-00'

const pid = localStorage.pid || '0'
const id = localStorage.currentId || '0'

export default defineComponent({
  name: 'Home',
  components: {
    Title,
    Item
  },
  setup() {
    let dom
    let timeId
    const { proxy } = getCurrentInstance()
    const courseList = ref([])
    const myList = ref([])
    const currentId = ref(parseInt(id))
    const isShow = ref(false)
    const pMap = ['', '', '']
    const cPid = ref(0)
    const cId = ref(0)

    const getCurrent = async () => {
      const { data } = await proxy.$http({
        method: 'get',
        url: '/current'
      })
      cPid.value = data.data.pid
      cId.value = data.data.id
    }
    getCurrent()

    const setCurrent = async () => {
      await proxy.$http({
        method: 'post',
        url: `/current?pid=${pid}&id=${id}`
      })
      getCurrent()
    }

    const getCourse = async () => {
      const loading = ElLoading.service()
      const { data } = await proxy.$http({
        method: 'get',
        url: `/course?pid=${pid}&id=${id}`
      })
      const str = data.data.sentences || ''
      if (str) {
        courseList.value = str.split('-').map((item) => parseInt(item))
      }
      myList.value = my00
      isShow.value = true
      nextTick(() => {
        dom = document.getElementById('vid')
      })
      loading.close()
    }

    const setF = async ({ isF, cid }) => {
      if (isF) {
        courseList.value.push(cid)
      } else {
        const index = courseList.value.findIndex((item) => item === cid)
        courseList.value.splice(index, 1)
      }
      const _sentences = courseList.value.join('-')
      const loading = ElLoading.service()
      await proxy.$http({
        method: 'put',
        url: `/course?pid=${pid}&id=${id}&sentences=${_sentences}`
      })
      loading.close()
    }

    const setAudioTime = (h, m, s, eh, em, es, isStop, fn) => {
      const time = h * 60 * 60 + m * 60 + s
      const etime = eh * 60 * 60 + em * 60 + es
      const len = etime - time
      dom.currentTime = time

      clearTimeout(timeId)
      dom.pause()
      if (isStop) {
        return
      }

      dom.play()
      timeId = setTimeout(() => {
        dom.pause()
        fn && fn()
      }, 1000 * len + 1000)
    }

    const refreshPage = () => {
      isShow.value = false
      nextTick(() => {
        isShow.value = true
      })
    }

    const refreshPage2 = () => {
      window.location.reload()
    }

    const toWords = () => {
      window.open('/words')
    }

    watch(currentId, () => {
      localStorage.currentId = currentId.value
      window.location.reload()
    })

    onMounted(() => {
      getCourse()
    })

    return {
      setAudioTime,
      refreshPage,
      refreshPage2,
      toWords,
      setF,
      setCurrent,
      pMap,
      cPid,
      cId,
      myList,
      currentId,
      isShow,
      courseList
    }
  }
})
</script>

